<template>
    <div class="blogList">
        <el-button @click="$router.push('blog')">新增</el-button>
        <div v-for="item in blogList" :key="item.id" class="blogWrapper" @click="editBlog(item.id)">
            {{item.title}}
            <i class="el-icon-delete" @click.stop="deleteBlog(item.id)"></i>

        </div>

    </div>
</template>
<script>
    export default {
        data() {
            return {
                blogList: []
            }
        },
        methods: {
            deleteBlog(id){
                this.$confirm('确定删除?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$post('blog/deleteBlog', {id})
                        .then(res => {
                            console.log(res)
                            if (res.code == 200) {
                                this.$message.success('删除成功');
                                this.getBlog();
                            }
                        })
                        .catch(err => {
                            console.log(err)
                        })
                })
                    .catch(() => {
                        console.log('!')
                        this.$message({
                            type: 'info',
                            message: '已取消删除'
                        });
                    });
            },
            getBlog() {
                console.log('啥情况')
                this.$post('blog/getBlogList', {type: ''})
                    .then(res => {
                        console.log(res)
                        if (res.code == 200) {
                            this.blogList = res.data
                        }
                    })
                    .catch(err => {
                        console.log(err)
                    })

            },
            editBlog(id){
                this.$router.push({
                    path:'blog',
                    query:{
                        id
                    }

                })
            }
        },
        mounted() {
            this.getBlog()
        }

    }
</script>
<style lang="less">
    .blogList {
        .blogWrapper {
            border: solid 1px red;
            border-bottom:none;
            cursor:pointer;
            line-height:30px;
        }

    }


</style>
